import React, { useState, useEffect } from 'react';
import s from './orders.module.css'
import { Redirect } from 'react-router-dom'







const Orders = (props) => {

    let [orders, setOrders] = useState(props.orders)
    let [value, setValue] = useState('')
    let [active, setActive] = useState('_id')
    useEffect(() => {
        setOrders(props.orders)
    }, [props.orders])

    function filter() {
        let filtered
        if (active === "_id") {
            filtered = props.orders.filter(i => i[active] === +value
            )
        } else {
            filtered = props.orders.filter(i => i[active] === value)
        }
        return setOrders(filtered)

    }

    if (!props.isAuth) { return <Redirect to='/' /> }


    return <div className={s.ordersWrapper}>
        <div className={s.title}>

            <h2>{orders.length ? " " : " "}</h2>
        </div>
        <div className={s.filtersWrapper}>
            <input type="text" value={value} onChange={e => setValue(e.target.value)} />
            <select>

                <option onClick={() => setActive("_id")}></option>
                <option onClick={() => setActive("secondName")}></option>
                <option onClick={() => setActive("phone")}></option>
            </select>
            <div className={s.btn} onClick={() => filter()}> </div>
            <div className={s.cancel} onClick={() => setOrders(props.orders)}> </div>

        </div>
        {orders.map(item => <div className={s.wrapper}>
            <div className={s.card}>
                <div className={s.id}>
                    {item._id}
                </div>
                <div className={s.name}>
                    <strong>{item.name}</strong>
                    <strong>{item.secondName}</strong>
                </div>
                <div className={s.phone}>
                    {item.phone}
                </div>
                <div>{item.products.map(i => <div className={s.productWrapper}>
                    <p> : {i.name}</p>
                    <p> : {i.count} </p>

                    <p>: {i.price}  </p>

                    <p>  : {i.count * i.price}</p>
                </div>)}
                </div>
                <div className={s.total}>
                    <strong>
                         : {item.total} {/*   */}
                    </strong>
                </div>
            </div>

            <div className={s.buttons}>
                <div className={s.btn} onClick={() => { props.onClick(item, 2) }}></div>
                <div className={s.lose} onClick={() => { props.onClick(item, 3) }}> </div>
            </div>
        </div>

        )}
    </div>





}

export default Orders;